
export default {
  name: 'Home',
  data() {
    return {
      activeIndex: '/home/firstPage',
      isAd: false,
    };
  },
  created() {
    if(this.$route.path=='/home/firstPage'){
      this.activeIndex=this.$route.path;
    }else if(this.$route.path=='/home/map'){
      this.activeIndex=this.$route.path;
    }else if(this.$route.path=="/home/identify"){
      this.activeIndex=this.$route.path;
    } else{
      this.activeIndex='/home/playground';
    }
  },
  watch:{
    '$route.path':function(newVal,oldVal){
      if(newVal=='/home/firstPage'){
        this.activeIndex=this.$route.path;
      }else if(newVal=='/home/map'){
        this.activeIndex=this.$route.path;
      }else if(newVal=="/home/identify"){
        this.activeIndex=this.$route.path;
      } else{
        this.activeIndex="/home/playground";
      }
    }
  }
}
