
import qs from "qs";
import ChineseDistricts from "@/assets/area";

var token =  localStorage.getItem('token')
export default {
  data() {
    return {
      headers: {token: token },
      formLabelWidth: '90px',
      fileList:[],
      ww: {},
      noeditor: true,
      dialogImageUrl: '',
      dialogVisible: false,
      dialogFormVisible: false,
      dynasties:[
        {value:"公元前"},{value:"夏"},{value:"商"},{value:"周"},{value:"秦"},{value:"汉"},{value:"三国"},{value:"晋"},{value:"南朝"},{value:"北朝"},{value:"隋"},{value:"唐"},{value:"宋"},{value:"辽"},{value:"西夏"},{value:"金"}, {value:"元"},{value:"明"},{value:"清"}
      ],
      ChineseDistricts: ChineseDistricts,
      province: [],
      shi: [],
      qu: [],
      city: [],
      block: [],
    };
  },
  methods: {
    // 加载china地点数据，三级
    getCityData() {
      let that = this;
      that.ChineseDistricts.forEach(function(item, index) {
        //省级数据
        that.province.push({
          id: item.code,
          value: item.name,
          children: item.cityList
        });
      });
    },
    // 选省
    choseProvince(e) {
      let that = this;
      that.city = [];
      that.block = [];
      that.ww.wwlocation2 = "";
      that.ww.wwlocation3 = "";
      for (var index2 in that.province) {
        if (e === that.province[index2].id) {
          that.shi = that.province[index2].children;
          that.ww.wwlocation1 = that.province[index2].value;

          that.shi.forEach(function(citem, cindex) {
            that.city.push({
              id: citem.code,
              value: citem.name,
              children: citem.areaList
            });
          });
        }
      }
    },
    // 选市
    choseCity(e) {
      let that = this;
      that.block = [];
      for (var index3 in that.city) {
        if (e === that.city[index3].id) {
          that.qu = that.city[index3].children;
          that.ww.wwlocation2 = that.city[index3].value;
          that.E = that.qu[0].id;
          that.qu.forEach(function(bitem, bindex) {
            that.block.push({
              id: bitem.code,
              value: bitem.name,
              children: []
            });
          });
        }
      }
    },
    // 选区
    choseBlock(e) {
      this.ww.wwlocation3 = e;
    },
    handleRemove(file) {
      this.$axios.delete("http://8.130.9.237:8083/ww/deleteImg?id="+file.id+"&name="+file.name).then(res=>{
        if(res.data.success){
          this.$message('删除成功');
          this.findAllImgs();
        }
      })
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleSuccess(file){
      this.findAllImgs();
      this.$message('上传成功');
    },
    handleError(file){
      this.$message('上传失败');
    },
    doing(){
      this.dialogFormVisible = false;
      this.save();
    },
    undoing(){
      this.dialogFormVisible = false;
    },
    save() {
      if(this.form.wwname == null || this.form.wwname == ""){
        this.$message("请输入文物名称");
        return;
      }else if(this.form.protectionlevel == null || this.form.protectionlevel ==""){
        this.$message("请选择保护级别");
        return;
      }else if(this.form.wwcategory == null || this.form.wwcategory ==""){
        this.$message("请选择文物种类");
        return;
      }else if(this.form.publishingbatch == null || this.form.publishingbatch ==""){
        this.$message("请输入公布批次");
        return;
      }else if(this.form.publishingyear == null || this.form.publishingyear ==""){
        this.$message("请输入公布年份");
        return;
      }else if(this.form.wwlocation1 == null || this.form.wwlocation1 ==""){
        this.$message("请输入所在省");
        return;
      }else if(this.form.wwlocation2 == null || this.form.wwlocation2 ==""){
        this.$message("请输入所在市");
        return;
      }else if(this.form.wwlocation3 == null || this.form.wwlocation3 ==""){
        this.$message("请输入所在区");
        return;
      }else if(this.form.specificlocation == null || this.form.specificlocation ==""){
        this.$message("请输入详细地址");
        return;
      }else if(this.form.wworganization == null || this.form.wworganization ==""){
        this.$message("请输入管理单位");
        return;
      }else if(this.form.wwlatitude1 == null || this.form.wwlatitude1 ==""){
        this.$message("请输入纬度");
        return;
      }else if(this.form.wwlongitude1 == null || this.form.wwlongitude1 ==""){
        this.$message("请输入经度");
        return;
      }else if(this.form.wwelevation == null || this.form.wwelevation ==""){
        this.$message("请输入海拔");
        return;
      }else if(this.form.wwyear == null || this.form.wwyear ==""){
        this.$message("请输入年份");
        return;
      }
      this.noeditor = true;
      this.$axios.post("http://8.130.9.237:8083/ww/update",qs.stringify(this.ww)).then(res=>{
        if(res.data.success){
          this.$message("保存成功！");
        }
      })
    },
    del() {
      let that = this;
      for(var k = 0; k < this.fileList.length;k++){
        this.$axios.delete("http://8.130.9.237:8083/ww/deleteImg?id="+this.fileList[k].id+"&name="+this.fileList[k].name).then(res=>{})
      }
      this.delAllFour();
      this.$axios.get('http://8.130.9.237:8083/test/findAllByparentId?parentId='+this.ww.wwid).then((res)=>{
        let testData = res.data.results;
        for(var i = 0 ; i < testData.length;i++){
          that.deleteTestImg(testData[i].id);
        }
      });
      this.$axios.get('http://8.130.9.237:8083/patrol/findAllByparentId?parentId='+this.ww.wwid).then((res)=>{
        let testData = res.data.results;
        for(var i = 0 ; i < testData.length;i++){
          that.deletePatrolImg(testData[i].id);
        }
      });
      this.$axios.delete("http://8.130.9.237:8083/ww/delete?id="+this.ww.wwid).then(res=>{
        if(res.data.success){
          this.$message("删除成功，跳转文物库");
          this.$router.push('/home/playground')
        }
      })
    },
    deleteTestImg(parentid){
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/test/findAllImgs?parentId='+parentid).then((res)=>{
        let files = res.data.results;
        for(var i = 0; i < files.length;i++){
          console.log(files[i].name);
          _this.$axios.delete("http://8.130.9.237:8083/test/deleteImg?id="+files[i].id+"&name="+files[i].name).then(res=>{});
        }
      });
    },
    deletePatrolImg(parentid){
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/patrol/findAllImgs?parentId='+parentid).then((res)=>{
        let files = res.data.results;
        for(var i = 0; i < files.length;i++){
          console.log(files[i].name);
          _this.$axios.delete("http://8.130.9.237:8083/patrol/deleteImg?id="+files[i].id+"&name="+files[i].name).then(res=>{})
        }
      });
    },
    delAllFour(){
      this.$axios.get('http://8.130.9.237:8083/four/findAllByModel?parentId='+this.ww.wwid).then((res)=>{
        let fileList1 = res.data.model1;
        let fileList2 = res.data.model2;
        let fileList3 = res.data.model3;
        let fileList4 = res.data.model4;
        for(var i = 0; i < fileList1.length;i++){
          this.$axios.delete("http://8.130.9.237:8083/four/delete?id="+fileList1[i].id+"&name="+fileList1[i].name).then(res=>{})
        };
        for(var j = 0; j < fileList2.length;j++){
          this.$axios.delete("http://8.130.9.237:8083/four/delete?id="+fileList2[j].id+"&name="+fileList2[j].name).then(res=>{})
        };
        for(var m = 0; m < fileList3.length;m++){
          this.$axios.delete("http://8.130.9.237:8083/four/delete?id="+fileList3[m].id+"&name="+fileList3[m].name).then(res=>{})
        };
        for(var n = 0; n < fileList4.length;n++){
          console.log(fileList4[n].name);
          this.$axios.delete("http://8.130.9.237:8083/four/delete?id="+fileList4[n].id+"&name="+fileList4[n].name).then(res=>{})
        };
      });
    },
    findAllImgs(){
      this.fileList=[];
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/ww/findAllByparentId?parentId='+this.ww.wwid).then((res)=>{
        let files = res.data.results;
        for(var i = 0; i < files.length;i++){
          var file = {
            id: files[i].id,
            name: files[i].name,
            url: "http://8.130.9.237:8083/image/"+files[i].name,
          }
          _this.fileList.push(file);
        }
      });
    },
  },
  created() {
    let ww=sessionStorage.getItem('wwinfo');
    this.ww = JSON.parse(ww);
    this.findAllImgs();
    this.getCityData();
  }
}
