
import qs from "qs";

export default {
  data() {
    return {
      data: [],
      picPath:"",
      defaultImg: 'this.src="' + require('@/assets/whitetower.png') + '"',
      token:"",
      loadInfo : '请选择图片',
      num:0,
    };
  },
  methods: {
    inww(ww){
      sessionStorage.setItem('wwinfo',JSON.stringify(ww));
      this.$router.push('/home/fadInfo')
    },
    getToken(){
      let that = this;
      this.$axios.get('http://8.130.9.237:8083/ww/getToken').then((res)=>{
        that.token=res.data.token;
      });
    },
    identify() {//图像识别
      this.loadInfo = '查找中...';
      this.data=[];//清空列表数据
      this.ww=[];
      let files = document.getElementById("id").files[0];
      let fileSize = 0;
      if (files) {
        fileSize = files.size;
        if (fileSize > 10 * 1024 * 1024) {
          alert("文件大小不能大于10M！");
          file.value = "";
          return false;
        } else if (fileSize <= 0) {
          alert("文件大小不能为0M！");
          file.value = "";
          return false;
        }
      } else {
        return false;
      }
      //转码base64
      let reader = new FileReader();
      let imgFile;
      reader.readAsDataURL(files);
      reader.onload = async e => {
        imgFile = e.target.result;
        let arr = imgFile.split(",");
        this.picPath='data:image/png;base64,'+arr[1]
        let that = this;
        //向百度智能云发出请求
        this.$axios.post("https://aip.baidubce.com/rest/2.0/image-classify/v2/advanced_general",
            qs.stringify({
              access_token:that.token,
              image: arr[1]
            })
        ).then((res)=>{
            that.results = res.data.result;
            that.loadInfo = '';
            let results = res.data.result;
            for(var i = 0; i < results.length; i++){
              that.find(results[i]);//find在数据库中匹配识别结果
            }
          });
      };
    },
    find(temp){//在数据库里模糊查找名称为temp.keyword的文物
      let that = this;
      this.$axios.get('http://8.130.9.237:8083/ww/find?wwname='+temp.keyword).then((res)=>{
        if(res.data.success){
          let results = res.data.results;
          for(var i = 0; i < results.length;i++){
            let ww = results[i];
            ww.score = temp.score;
            that.data.push(ww);
            that.num=that.data.length;
          }
        }
      });
    }
  },
  created() {
    this.getToken();
  }
};
