
import qs from "qs";
import SIdentify from "../SIdentify";
export default {
  name: "register",
  components: {SIdentify},
  data(){
    var isMobileNumber= (rule, value, callback) => {
      if (!value) {
        return new Error("请输入电话号码");
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
        const isPhone = reg.test(value);
        value = Number(value); //转换为数字
        if (typeof value === "number" && !isNaN(value)) {//判断是否为数字
          value = value.toString(); //转换成字符串
          if (value.length < 0 || value.length > 12 || !isPhone) { //判断是否为11位手机号
            callback(new Error("手机号码格式如:138xxxx8754"));
          } else {
            callback();
          }
        } else {
          callback(new Error("请输入电话号码"));
        }
      }
    };
    var isEmail = (rule, value, callback) => {
      if (!value) {
        callback();
      } else {
        const reg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;
        const email = reg.test(value);
        if (!email) {
          callback(new Error("邮箱格式如:admin@163.com"));
        } else {
          callback();
        }
      }
    };
    const validateVerifycode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入验证码'))
      } else if (value !== this.identifyCode) {
        callback(new Error('验证码不正确!'))
      } else {
        callback()
      }
    };
    return {
      rpassword:'',
      user: {
        authority: 0,
        username:"",
        password:"",
        telephone:"",
        email:"",
        verifycode:"",
      },
      identifyCodes: '1234567890',
      identifyCode: '',
      conum:'',
      rules: {
        telephone:[
          { required: true, message: "请输入手机号码", trigger: "blur" },
          { validator: isMobileNumber, trigger: "blur" }
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: isEmail, trigger: "blur" }
        ],
        verifycode: [
          { required: true, trigger: 'blur', validator: validateVerifycode }
        ],
      }
    }
  },
  methods:{
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min)
    },
    // 切换验证码
    refreshCode() {
      this.identifyCode = ''
      this.makeCode(this.identifyCodes, 4)
    },
    // 生成四位随机验证码
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
            this.randomNum(0, this.identifyCodes.length)
            ]
      }
    },
    register(user){//注册函数
      if(this.user.username == "" ||
          this.user.password == "" ||
          this.user.email == "" ||
          this.user.telephone == "" ||
          this.rpassword == ""){
        this.$message('请正确填写全部信息！');
        return;
      }else if(this.rpassword != this.user.password){
        this.$message('密码不一致！');
        return;
      }//检查信息是否全部填写、密码是否一致
      this.$refs[user].validate(valid => {
        if (valid) {//如果电话号码和邮箱格式正确
          this.$axios.post('http://8.130.9.237:8083/user/add',
              qs.stringify(this.user)
          ).then((res)=>{
            if(res.data.success){
              this.$message('注册成功，三秒后跳转登陆页面');//弹出信息框进行提示
              this.threeGo();//注册成功三秒后跳转登陆页面
            }else{
              this.$message('该用户名已存在！');
            }
          });
        } else {//如果电话号码和邮箱格式不正确
          this.$message('请正确填写信息');
        }
      });
    },
    threeGo(){//三秒跳转函数
      const TIME_COUNT = 3;
      if(!this.timer){
        this.conum = TIME_COUNT;
        this.timer = setInterval(()=>{
          if(this.conum > 0 && this.conum <= TIME_COUNT){
            this.conum--;
          }else{
            clearInterval(this.timer);
            this.timer = null;
            //跳转路径
            this.$router.push("/welcome/login");
          }
        },1000)
      }
    },
  }
}
