
import qs from "qs";
var token =  localStorage.getItem('token')
export default {
  data() {
    return {
      headers: {token: token },
      formLabelWidth: '90px',
      dialogFormVisible: false,
      dialogImageUrl: '',
      dialogVisible: false,
      fileList: [],
      test:{},
      ww:{},
    };
  },
  methods: {
    doing(){
      if(this.test.height== null || this.test.height == ""){
        this.$message("请输入高度");
        return;
      }else if(this.test.length == null || this.test.length ==""){
        this.$message("请输入长度");
        return;
      }else if(this.test.width == null || this.test.width ==""){
        this.$message("请输入宽度");
        return;
      }else if(this.test.temp == null || this.test.temp ==""){
        this.$message("请输入温度");
        return;
      }else if(this.test.humidity == null || this.test.humidity ==""){
        this.$message("请输入湿度");
        return;
      }else if(this.test.airquality == null || this.test.airquality ==""){
        this.$message("请输入空气质量");
        return;
      }else if(this.test.displaceme == null || this.test.displaceme ==""){
        this.$message("请输入位移");
        return;
      }else if(this.test.status == null || this.test.status ==""){
        this.$message("请选择保存状态");
        return;
      }else if(this.test.smallchange == null || this.test.smallchange ==""){
        this.$message("请输入细微变化");
        return;
      }
      this.dialogFormVisible = false;
      this.$axios.post('http://8.130.9.237:8083/test/update',
          qs.stringify(this.test)
      ).then((res)=>{
        if(res.data.success){
          this.$message('修改成功');
        }
      });
    },
    undoing(){
      this.dialogFormVisible = false;
    },
    del(){
      this.$axios.delete("http://8.130.9.237:8083/test/delete?id="+this.test.id).then(res=>{
        if(res.data.success){
          this.$message("删除成功");
          this.$router.push('/home/administrator/test')
        }
      })
    },
    handleRemove(file) {
      this.$axios.delete("http://8.130.9.237:8083/test/deleteImg?id="+file.id+"&name="+file.name).then(res=>{
        if(res.data.success){
          this.$message('删除成功');
        }
      })
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      console.log(file.url);
      this.dialogVisible = true;
    },
    handleSuccess(file){
      this.$message('上传成功,刷新数据');
      this.findAllImg();
    },
    handleError(file){
      this.$message('上传失败');
    },
    preview(file){
      let a = document.createElement("a");
      a.href ='http://8.130.9.237:8083/test/preview?name='+file.name;
      document.body.appendChild(a);
      a.click();  //下载
      URL.revokeObjectURL(a.href);    // 释放URL 对象
      document.body.removeChild(a);   // 删除 a 标签
    },
    findAllImg(){
      this.fileList=[];
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/test/findAllImgs?parentId='+this.test.id).then((res)=>{
        let files = res.data.results;
        for(var i = 0; i < files.length;i++){
          var file = {
            id: files[i].id,
            name: files[i].name,
            url: "http://8.130.9.237:8083/image/test/"+files[i].name,
          }
          _this.fileList.push(file);
        }
      });
    },
  },
  created() {
    let test=sessionStorage.getItem('test');
    this.test = JSON.parse(test);
    let ww=sessionStorage.getItem('wwinfo');
    this.ww = JSON.parse(ww);
    console.log(this.test);
    this.findAllImg();
  }
}
