
import qs from "qs";
var token =  localStorage.getItem('token')
export default {
  data() {
    return {
      headers: {token: token },
      fileList: [],
      patrol:{},
      ww:{},
      dialogFormVisible: false,
      dialogVisible: false,
      dialogImageUrl: '',
      formLabelWidth: '130px',
    };
  },
  methods: {
    doing(){
      if(this.patrol.wwprotection== null || this.patrol.wwprotection == ""){
        this.$message("请输入文物保护");
        return;
      }else if(this.patrol.environmentprotection == null || this.patrol.environmentprotection ==""){
        this.$message("请输入环境保护");
        return;
      }else if(this.patrol.suggestion == null || this.patrol.suggestion ==""){
        this.$message("请输入保护单位整改意见");
        return;
      }else if(this.patrol.illegal == null || this.patrol.illegal ==""){
        this.$message("请输入违法行为");
        return;
      }else if(this.patrol.protectionproject == null || this.patrol.protectionproject ==""){
        this.$message("请输入保护工程");
        return;
      }
      this.dialogFormVisible = false;
      this.$axios.post('http://8.130.9.237:8083/patrol/update',
          qs.stringify(this.patrol)
      ).then((res)=>{
        if(res.data.success){
          this.$message('修改成功');
        }
      });
    },
    undoing(){
      this.dialogFormVisible = false;
    },
    del(){
      this.$axios.delete("http://8.130.9.237:8083/patrol/delete?id="+this.patrol.id).then(res=>{
        if(res.data.success){
          this.$message("删除成功");
          this.$router.push('/home/administrator/patrol')
        }
      })
    },
    handleSuccess(file){
      this.$message('上传成功,刷新数据');
      this.findAllImg();
    },
    handleError(file){
      this.$message('上传失败');
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      console.log(file.url);
      this.dialogVisible = true;
    },
    handleRemove(file, fileList){
      this.$axios.delete("http://8.130.9.237:8083/patrol/deleteImg?id="+file.id+"&name="+file.name).then(res=>{
        if(res.data.success){
          this.$message('删除成功');
        }
      })
    },
    /*
    download(file){
      let a = document.createElement("a");
      a.href ='http://8.130.9.237:8083/patrol/download?name='+file.name;
      document.body.appendChild(a);
      a.click();  //下载
      URL.revokeObjectURL(a.href);    // 释放URL 对象
      document.body.removeChild(a);   // 删除 a 标签
    },
    preview(file){
      let a = document.createElement("a");
      a.href ='http://8.130.9.237:8083/patrol/preview?name='+file.name;
      document.body.appendChild(a);
      a.click();  //下载
      URL.revokeObjectURL(a.href);    // 释放URL 对象
      document.body.removeChild(a);   // 删除 a 标签
    },
     */
    findAllImg(){
      this.fileList=[];
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/patrol/findAllImgs?parentId='+this.patrol.id).then((res)=>{
        let files = res.data.results;
        for(var i = 0; i < files.length;i++){
          var file = {
            id: files[i].id,
            name: files[i].name,
            url: "http://8.130.9.237:8083/image/patrol/"+files[i].name,
          }
          _this.fileList.push(file);
        }
      });
    },
  },
  created() {
    let patrol=sessionStorage.getItem('patrol');
    this.patrol = JSON.parse(patrol);
    let ww=sessionStorage.getItem('wwinfo');
    this.ww = JSON.parse(ww);
    this.findAllImg();
  }
}
