
import qs from "qs";

export default {
  data() {
    return {
      patrolData: [],
      dialogFormVisible: false,
      patrolInfo:{},
      formLabelWidth: '130px',
      ww:{},
      user:{},
      input:"",
      fileList:[],
    }
  },
  methods:{
    findLike(){
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/patrol/findLike?parentId='+this.ww.wwid+'&date='+this.input).then((res)=>{
        _this.patrolData = res.data.results;
      })
    },
    doing(){
      if(this.patrolInfo.wwprotection== null || this.patrolInfo.wwprotection == ""){
        this.$message("请输入文物保护");
        return;
      }else if(this.patrolInfo.environmentprotection == null || this.patrolInfo.environmentprotection ==""){
        this.$message("请输入环境保护");
        return;
      }else if(this.patrolInfo.suggestion == null || this.patrolInfo.suggestion ==""){
        this.$message("请输入保护单位整改意见");
        return;
      }else if(this.patrolInfo.illegal == null || this.patrolInfo.illegal ==""){
        this.$message("请输入违法行为");
        return;
      }else if(this.patrolInfo.protectionproject == null || this.patrolInfo.protectionproject ==""){
        this.$message("请输入保护工程");
        return;
      }
      this.dialogFormVisible = false;
      this.$axios.post('http://8.130.9.237:8083/patrol/add',
          qs.stringify(this.patrolInfo)
      ).then((res)=>{
        if(res.data.success){
          this.$message('添加成功，跳转详情页面添加图片');
          let patrol = res.data.patrol;
          sessionStorage.setItem('patrol',JSON.stringify(patrol));
          this.$router.push('/home/administrator/patrolInfo')
        }
      });
    },
    undoing(){
      this.dialogFormVisible = false;
    },
    delRow(value){
      this.deleteAllImg(value.id);
      this.$axios.delete("http://8.130.9.237:8083/patrol/delete?id="+value.id).then(res=>{
        if(res.data.success){
          this.$message("删除成功");
          this.findAllByparentId();
        }
      })
    },
    deleteAllImg(parentid){
      this.fileList=[];
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/patrol/findAllImgs?parentId='+parentid).then((res)=>{
        let files = res.data.results;
        for(var i = 0; i < files.length;i++){
          _this.$axios.delete("http://8.130.9.237:8083/patrol/deleteImg?id="+files[i].id+"&name="+files[i].name).then(res=>{})
        }
      });
    },
    findAllByparentId(){
      this.$axios.get('http://8.130.9.237:8083/patrol/findAllByparentId?parentId='+this.ww.wwid).then((res)=>{
        this.patrolData = res.data.results;
        for(var i = 0; i < this.patrolData.length; i++){
          this.patrolData[i].date = this.patrolData[i].date.substring(0,10);
        }
      });
    },
    look(value){
      sessionStorage.setItem('patrol',JSON.stringify(value));
      this.$router.push('/home/administrator/patrolInfo')
    },
  },
  created() {
    let user=sessionStorage.getItem('info');
    this.user = JSON.parse(user);
    let ww=sessionStorage.getItem('wwinfo');
    this.ww = JSON.parse(ww);
    this.findAllByparentId();
    this.patrolInfo.parentid=this.ww.wwid;
    this.patrolInfo.username=this.user.username;
  }
}
