
import SIdentify from "../SIdentify";
import qs from "qs";

export default {
  name: "login",
  data(){
    const validateVerifycode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入验证码'))
      } else if (value !== this.identifyCode) {
        callback(new Error('验证码不正确!'))
      } else {
        callback()
      }
    };
    return {
      user:{
        verifycode:"",
      },
      identifyCodes: '1234567890',
      identifyCode: '',
      rules: {
        verifycode: [
          { required: true, trigger: 'blur', validator: validateVerifycode }
        ],
      }
    }
  },
  components: {
    SIdentify
  },
  methods:{
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min)
    },
    // 切换验证码
    refreshCode() {
      this.identifyCode = ''
      this.makeCode(this.identifyCodes, 4)
    },
    // 生成四位随机验证码
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
            this.randomNum(0, this.identifyCodes.length)
            ]
      }
    },
    login(user){
      if(this.user.username == "" || this.user.password == ""){
        this.$message('请确保用户名和密码不为空');
        return;
      }//检查是否有信息为空
      this.$refs[user].validate(valid => {
        if (valid) {//验证码正确
          this.$axios.post('http://8.130.9.237:8083/user/findOne',
              qs.stringify(this.user)
          ).then((res)=>{
            if(res.data.success){//用户信息正确
              let token = res.data.token;
              localStorage.setItem("token",token);//本地存入token
              sessionStorage.setItem('info',JSON.stringify(res.data.user));//用户信息存入session
              this.$router.push('/home/firstPage')//跳转到首页
            }else{//用户信息不正确
              if(res.data.msg == "用户不存在"){
                this.$message('用户不存在！');
              }else if(res.data.msg == "密码错误"){
                this.$message('密码错误！');
              }
            }
          });
        } else {//验证码错误
          this.$message('请正确填写信息');
        }
      });
    },
  }
}
