
import blackLogo1 from '@/assets/blackLogo1.png'
let BMap = {};
let map = {};
let ww = [];
let infoWindows=[];
let points=[]
export default {
      data(){
        return{
          places: [],
          state1: '',
          wwid:null,
        }
      },
      methods:{
        inww(){
          this.$axios.get('http://8.130.9.237:8083/ww/findOneByPk?id='+this.wwid).then((res)=>{
            var ww = res.data;
            sessionStorage.setItem('wwinfo',JSON.stringify(ww));
            this.$router.push('/home/fadInfo')
          });
        },
        findAll(){
          this.$axios.get('http://8.130.9.237:8083/ww/findAll').then((res)=>{
            ww = res.data.results;
            for(var i = 0;i <ww.length;i++){
              var place = {
                value:ww[i].wwname,
                wwid:ww[i].wwid,
                  y:ww[i].wwlatitude1,
                  x:ww[i].wwlongitude1,
                  i:i,
              }
              this.places.push(place);
            }
          });
        },
        getAddrByPoint(point,i){
          map.centerAndZoom(point, 10);
          map.openInfoWindow(infoWindows[i],points[i]);
        },
        loadJScript() {
          var script = document.createElement('script');
          script.type = 'text/javascript';
          script.src = 'https://api.map.baidu.com/getscript?v=3.0&ak=DNMsvCZA5BgKYpCEffHexGiBkha4KVDG&services=&t=20220315120524';
          script.onload = ()=>{
            this.init();
          }
          document.body.appendChild(script);
        },
        init() {
          var that = this;
          BMap = window.BMap;
          map = new BMap.Map("allmap");    // 创建Map实例
          map.centerAndZoom(new BMap.Point(115.97, 40.45), 10);  // 初始化地图,设置中心点坐标和地图级
          //添加地图类型控件
          map.addControl(new BMap.MapTypeControl({
            mapTypes:[
              BMAP_NORMAL_MAP,
              BMAP_HYBRID_MAP,
            ]}));
          var navigationControl = new BMap.NavigationControl({ //创建一个特定样式的地图平移缩放控件
            anchor: BMAP_ANCHOR_TOP_LEFT, //靠右上角位置
            type: BMAP_NAVIGATION_CONTROL_SMALL //SMALL控件类型
          })
          map.addControl(navigationControl); //将控件添加到地图
          var geolocationControl = new BMap.GeolocationControl({anchor: BMAP_ANCHOR_BOTTOM_LEFT}) ;//创建一个地图定位控件
          geolocationControl.addEventListener("locationSuccess", function(e){ //绑定定位成功后事件
            that.getAddrByPoint(e.point); //定位成功后调用逆地址解析函数
          });
          geolocationControl.addEventListener("locationError",function(e){ //绑定定位失败后事件
            alert(e.message);
          });
          map.addControl(geolocationControl); //将控件添加到地图
          map.setCurrentCity("北京");          // 设置地图显示的城市 此项是必须设置的
          map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放
        },
        addMarker(x,y,name,location1,location2,location3,location4,i){
          var myIcon = new BMap.Icon(blackLogo1, new BMap.Size(400, 400),{
            anchor: new BMap.Size(16, 20)
          });
          points[i] = new BMap.Point(x,y);
          var marker = new BMap.Marker(points[i],{ icon: myIcon }); // 创建点
          map.addOverlay(marker);            //增加点
          var opts = {
            width : 300,     // 信息窗口宽度
            height: 80,     // 信息窗口高度
            title : name , // 信息窗口标题
          }
          infoWindows[i] = new BMap.InfoWindow("地址:"+location1+location2+location3+location4, opts);  // 创建信息窗口对象
          marker.addEventListener("click", function(){
            map.openInfoWindow(infoWindows[i],marker.point); //开启信息窗口
          });
        },
        makePoint(){
          for(var i = 0; i < ww.length;i++){
            this.addMarker(ww[i].wwlongitude1  - 0, ww[i].wwlatitude1- 0,ww[i].wwname,ww[i].wwlocation1,ww[i].wwlocation2,ww[i].wwlocation3,ww[i].specificlocation,i);
          }
        },
        querySearch(queryString, cb) {
          var places = this.places;
          var results = queryString ? places.filter(this.createFilter(queryString)) : places;
          cb(results);
        },
        createFilter(queryString) {
          return (places) => {
            return (places.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
          };
        },
        handleSelect(item) {
          this.getAddrByPoint(new BMap.Point(item.x- 0, item.y- 0),item.i);
          this.wwid = item.wwid - 0;
        }
      },
      mounted() {
          this.findAll();
          setTimeout(()=>{
            this.loadJScript();
          },300);
          setTimeout(()=>{
            this.makePoint();
          },1000);
      },
};
