
import qs from "qs";

export default {
  data(){
    var isMobileNumber= (rule, value, callback) => {
      if (!value) {
        return new Error("请输入电话号码");
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
        const isPhone = reg.test(value);
        value = Number(value); //转换为数字
        if (typeof value === "number" && !isNaN(value)) {//判断是否为数字
          value = value.toString(); //转换成字符串
          if (value.length < 0 || value.length > 12 || !isPhone) { //判断是否为11位手机号
            callback(new Error("手机号码格式如:138xxxx8754"));
          } else {
            callback();
          }
        } else {
          callback(new Error("请输入电话号码"));
        }
      }
    };
    var isEmail = (rule, value, callback) => {
      if (!value) {
        callback();
      } else {
        const reg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;
        const email = reg.test(value);
        if (!email) {
          callback(new Error("邮箱格式如:admin@163.com"));
        } else {
          callback();
        }
      }
    };
    return {
      user:{},
      users:[],
      infomationShow: true,
      color1: "white",
      color2: "#2A514D",
      adShow:false,
      status:"管理员",
      noeditor: true,
      input:'',
      dialogFormVisible: false,
      formLabelWidth: '120px',
      repassword:"",
      rules: {
        telephone:[
          { required: true, message: "请输入手机号码", trigger: "blur" },
          { validator: isMobileNumber, trigger: "blur" }
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: isEmail, trigger: "blur" }
        ],
      }
    }
  },
  methods:{
    infomationClick(){    //个人信息事件
      var temp = this.color1;
      this.color1=this.color2;
      this.color2=temp;
      this.infomationShow = true
      this.adShow = false
    },

    administratorClick(){     //用户列表事件
      var temp = this.color1;
      this.color1=this.color2;
      this.color2=temp;
      this.findAll();
      this.infomationShow = false
      this.adShow = true
    },
    findAll(){
      this.$axios.get('http://8.130.9.237:8083/user/findAll').then((res)=>{
        this.users = res.data.results;
      });
    },
    findLike(){
      this.$axios.get('http://8.130.9.237:8083/user/findLike?username='+this.input).then((res)=>{
        this.users = res.data;
      });
    },
    delRow(user){
      this.$axios.delete("http://8.130.9.237:8083/user/delete?username="+user.username).then(res=>{
        if(res.data.success){
          this.$message("删除用户成功");
          this.users.splice(this.users.indexOf(user),1);
        }
      })
    },
    changeSwitch(row){
      this.updateUserInfo(row);
    },
    updateUserInfo(user){
          this.$axios.post('http://8.130.9.237:8083/user/update',
              qs.stringify(user)
          ).then((res)=>{
            if(res.data.success){
              this.$message('权限更改成功！');
            }
          });
    },
    logout(){
      sessionStorage.clear();
      this.$message('退出成功！');
      this.$router.push('/welcome/welcomeText')
    },
    updateInfo(user){
      if(this.user.password == "" ||
          this.user.email == "" ||
          this.user.telephone == "" ||
          this.rpassword == ""){
        this.$message('请正确填写全部信息！');
        return;
      }else if(this.repassword != this.user.password){
        this.$message('密码不一致！');
        return;
      }
      this.$refs[user].validate(valid => {
        if (valid) {
          this.$axios.post('http://8.130.9.237:8083/user/update',
              qs.stringify(this.user)
          ).then((res) => {
            if (res.data.success) {
              this.$message('修改成功！');
              sessionStorage.setItem('info', JSON.stringify(this.user));
            }
          });
        } else {
          this.$message('请正确填写信息');
        }
      });
    },
    doing(user){
      this.dialogFormVisible = false;
      this.updateInfo(user);
    },
    undoing(){
      this.dialogFormVisible = false;
    }
  },
  created(){
    let user=sessionStorage.getItem('info');
    this.user = JSON.parse(user);
    if(this.user.authority==0){
      this.status="普通用户";
    }else{
      this.status="管理员";
    };
  },
}
