import {createRouter, createWebHashHistory} from 'vue-router'
import Home from '../views/Home.vue'
import Welcome from "../views/Welcome";
import welcomeText from "../components/visitor/welcomeText";
import login from "../components/visitor/login";
import register from "../components/visitor/register";
import firstPage from "../components/visitor/firstPage";
import map from "../components/visitor/map";
import playground from "../components/visitor/playground";
import fadInfo from "../components/visitor/fadInfo";
import Administrator from "../components/administrator/Administrator";
import basic from "../components/administrator/basic";
import four from "../components/administrator/four";
import patrol from "../components/administrator/patrol";
import test from "../components/administrator/test";
import patrolInfo from "../components/administrator/patrolInfo";
import testInfo from "../components/administrator/testInfo";
import userCenter from "../views/userCenter";
import identify from "../components/visitor/identify"

const routes = [
  {path: '/',redirect:'/welcome/welcomeText'},
  {
    path: '/home',component: Home,
    children: [
      {path: 'firstPage', component: firstPage},
      {path: 'map', component: map},
      {path: 'playground',component: playground},
      {path:'fadInfo',component: fadInfo},
      {path:'identify',component: identify},
      {
        path: 'administrator',component: Administrator,
        children:[
          {path:'basic',component:basic},
          {path: 'four',component: four},
          {path: 'patrol',component: patrol},
          {path:'patrolInfo',component:patrolInfo},
          {path: 'test',component: test},
          {path: 'testInfo',component: testInfo}
        ]
      }
    ]
  },
  {
    path: '/welcome',component: Welcome,
    children:[
      {path:'login',component:login},//子路由path不需要加/
      {path:'welcomeText',component:welcomeText},
      {path:'register',component:register}
    ]
  },
  {path: '/userCenter',component: userCenter},
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to,from,next)=>{
  if( to.path !=='/welcome/login' && to.path !=="/welcome/register" && to.path !== "/welcome/welcomeText" && sessionStorage.getItem('info') === null) {
    next({path:'/welcome/login'})
  }
  else next()
})

export default router