
var token =  localStorage.getItem('token')
export default {
  data() {
    return {
      headers: {token: token },
      fileList1: [],
      fileList2: [],
      fileList3: [],
      fileList4: [],
      ww:{},
    };
  },
  methods: {
    handleSuccess(file){
      this.findAllByModel();
      this.$message('上传成功');
    },
    handleError(file){
      this.$message('上传失败');
    },
    delRow(file){
      this.$axios.delete("http://8.130.9.237:8083/four/delete?id="+file.id+"&name="+file.name).then(res=>{
        if(res.data.success){
          this.$message("删除成功");
          this.findAllByModel();
        }
      })
    },
    download(file){
      var xhr = new XMLHttpRequest();
      xhr.open('GET', 'http://8.130.9.237:8083/four/download?name='+file.name,true);
      xhr.setRequestHeader("token",localStorage.getItem('token'));//请求头加上token
      xhr.responseType='blob';
      xhr.onload=function(e){
        //如果请求执行成功
        if(this.status==200){
          var blob=this.response;
          var a =document.createElement('a');//定义a标签
          var url=URL.createObjectURL(blob);
          a.href=url;
          a.download=file.name;
          a.click();//点击下载
          window.URL.revokeObjectURL(url);
        }
      };
      xhr.send();
    },
    preview(file){
      let a = document.createElement("a");
      a.href ='http://8.130.9.237:8083/four/preview?name='+file.name;
      document.body.appendChild(a);
      a.click();  //下载
      URL.revokeObjectURL(a.href);    // 释放URL 对象
      document.body.removeChild(a);   // 删除 a 标签
    },
    findAllByModel(){
      this.$axios.get('http://8.130.9.237:8083/four/findAllByModel?parentId='+this.ww.wwid).then((res)=>{
        this.fileList1 = res.data.model1;
        this.fileList2 = res.data.model2;
        this.fileList3 = res.data.model3;
        this.fileList4 = res.data.model4;
      });
    },
  },
  created() {
    let ww=sessionStorage.getItem('wwinfo');
    this.ww = JSON.parse(ww);
    this.findAllByModel();
  }
}
