
export default {
  data() {
    return {
      img:[],
      ww: {},
      loading:true,
    };
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    findImg(){
      let _this = this;
      _this.$axios.get('http://8.130.9.237:8083/ww/findAllByparentId?parentId='+_this.ww.wwid).then((res)=>{
        let files = res.data.results;
        for(var i = 0; i < files.length;i++){
          let img = {
            url:"http://8.130.9.237:8083/image/"+files[i].name
          };
          _this.img.push(img);
        }
        _this.loading=false;
      });
    },
  },
  created() {
    let user =sessionStorage.getItem('info');
    let ww = sessionStorage.getItem('wwinfo')
    user = JSON.parse(user);
    this.ww = JSON.parse(ww);
    this.findImg();
  },
}
