
import qs from "qs";
import docxtemplater from 'docxtemplater'
import PizZip from 'pizzip'
import JSZipUtils from 'jszip-utils'
import {saveAs} from 'file-saver'
export default {
  data() {
    return {
      testData: [],
      dialogFormVisible: false,
      testInfo:{},
      formLabelWidth: '90px',
      ww:{},
      date:"",
      wwname:"",
      stats:[
        {value:"优"},{value:"良好"},{value:"一般"},{value:"一般"}
      ],
      user:{},
      input:"",
    }
  },
  methods:{
    findLike(){
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/test/findLike?parentId='+this.ww.wwid+'&date='+this.input).then((res)=>{
        _this.testData = res.data.results;
      })
    },
    dateFormat(){
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1 < 10 ?
          '0' + (date.getMonth() + 1) : date.getMonth()+ 1
      var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
      return year + '-' +month + '-' + day;
    },
    delRow(value){
      this.deleteAllImg(value.id);
      this.$axios.delete("http://8.130.9.237:8083/test/delete?id="+value.id).then(res=>{
        if(res.data.success){
          this.$message("删除成功");
          this.findAllByparentId();
        }
      })
    },
    deleteAllImg(parentid){
      this.fileList=[];
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/test/findAllImgs?parentId='+parentid).then((res)=>{
        let files = res.data.results;
        for(var i = 0; i < files.length;i++){
          _this.$axios.delete("http://8.130.9.237:8083/test/deleteImg?id="+files[i].id+"&name="+files[i].name).then(res=>{});
        }
      });
    },
    look(value){
      sessionStorage.setItem('test',JSON.stringify(value));
      this.$router.push('/home/administrator/testInfo')
    },
    doing(){
      if(this.testInfo.height== null || this.testInfo.height == ""){
        this.$message("请输入高度");
        return;
      }else if(this.testInfo.length == null || this.testInfo.length ==""){
        this.$message("请输入长度");
        return;
      }else if(this.testInfo.width == null || this.testInfo.width ==""){
        this.$message("请输入宽度");
        return;
      }else if(this.testInfo.temp == null || this.testInfo.temp ==""){
        this.$message("请输入温度");
        return;
      }else if(this.testInfo.humidity == null || this.testInfo.humidity ==""){
        this.$message("请输入湿度");
        return;
      }else if(this.testInfo.airquality == null || this.testInfo.airquality ==""){
        this.$message("请输入空气质量");
        return;
      }else if(this.testInfo.displaceme == null || this.testInfo.displaceme ==""){
        this.$message("请输入位移");
        return;
      }else if(this.testInfo.status == null || this.testInfo.status ==""){
        this.$message("请选择保存状态");
        return;
      }else if(this.testInfo.smallchange == null || this.testInfo.smallchange ==""){
        this.$message("请输入细微变化");
        return;
      }
      this.dialogFormVisible = false;
      this.$axios.post('http://8.130.9.237:8083/test/add',
          qs.stringify(this.testInfo)
      ).then((res)=>{
        if(res.data.success){
          this.$message('添加成功，跳转详情页面添加图片');
          let test = res.data.test;
          sessionStorage.setItem('test',JSON.stringify(test));
          this.$router.push('/home/administrator/testInfo')
        }
      });
    },
    undoing(){
      this.dialogFormVisible = false;
    },
    findAllByparentId(){
      this.$axios.get('http://8.130.9.237:8083/test/findAllByparentId?parentId='+this.ww.wwid).then((res)=>{
        this.testData = res.data.results;
      });
    },
    /**
     * description： 导出文档
     */
    ept(){
      let _this = this;
      // 读取并获得模板文件的二进制内容
      JSZipUtils.getBinaryContent("/dist/demo.docx",function(error, content) {
        // demo.docx是模板。我们在导出的时候，会根据此模板来导出对应的数据
        // 抛出异常
        if (error) {
          throw error;
        }
        let zip = new PizZip(content);
        // 创建并加载docxtemplater实例对象
        let doc = new docxtemplater();
        doc.loadZip(zip);
        // 设置模板变量的值
        doc.setData({
          ww: _this.ww,
          test:_this.testData,
          wwname:_this.wwname,
          date:_this.date
        });
        try {
          // 用模板变量的值替换所有模板变量
          doc.render();
        } catch (error) {
          // 抛出异常
          let e = {
            message: error.message,
            name: error.name,
            stack: error.stack,
            properties: error.properties
          };
          throw error;
        }
        // 生成一个代表docxtemplater对象的zip文件（不是一个真实的文件，而是在内存中的表示）
        let out = doc.getZip().generate({
          type: "blob",
          mimeType:"application/vnd.openxmlformats-officedocument.wordprocessingml.document"
        });
        // 将目标文件对象保存为目标类型的文件，并命名
        saveAs(out, _this.ww.wwname+".docx");
      });
    },
  },
  created() {
    let user=sessionStorage.getItem('info');
    this.user = JSON.parse(user);
    let ww=sessionStorage.getItem('wwinfo');
    this.ww = JSON.parse(ww);
    this.findAllByparentId();
    this.testInfo.parentid=this.ww.wwid;
    this.testInfo.username=this.user.username;
    this.date = this.dateFormat();
    this.wwname=this.ww.wwname;
  }
}
