
import qs from "qs";
import ChineseDistricts from "@/assets/area";

export default {
  data() {
    return {
      sortList:[
        {name:"古文化遗址"}, {name:"古墓葬"}, {name:"古建筑"}, {name:"石窟寺"}, {name:"石刻"}, {name:"壁画"}, {name:"其他"}
      ],
     levelList:[
        {name:"国保"}, {name:"省保"}, {name:"市保"}, {name:"县保"}
      ],
      dynasties:[
        {value:"公元前"},{value:"夏"},{value:"商"},{value:"周"},{value:"秦"},{value:"汉"},{value:"三国"},{value:"晋"},{value:"南朝"},{value:"北朝"},{value:"隋"},{value:"唐"},{value:"宋"},{value:"辽"},{value:"西夏"},{value:"金"}, {value:"元"},{value:"明"},{value:"清"}
      ],
      ChineseDistricts: ChineseDistricts,
      province1: [],
      shi1: [],
      qu1: [],
      city1: [],
      block1: [],
      province2: [],
      shi2: [],
      qu2: [],
      city2: [],
      block2: [],
      wwname:"",
      pname: "", //省的名字
      cname: "", //市的名字
      bname: "", //区的名字
      dynasty:"",
      level:"",
      sort:"",
      ww:[],
      dialogFormVisible: false,
      form: {
        serialnum: '',
        registerid: '',
      },
      formLabelWidth: '90px',
      user:{},
    };
  },
  methods:{
    inad(ww){
      sessionStorage.setItem('wwinfo',JSON.stringify(ww));
      this.$router.push('/home/administrator/basic');
    },
    inww(ww){
      sessionStorage.setItem('wwinfo',JSON.stringify(ww));
      this.$router.push('/home/fadInfo')
    },
    dateFormat(){
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1 < 10 ?
          '0' + (date.getMonth() + 1) : date.getMonth()+ 1
      var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
      var hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
      var minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
      var seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      return year + month + day +hours+minutes+seconds;
    },
    findAll(){
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/ww/findAll').then((res)=>{
        _this.ww = res.data.results;
        let newarr = [];
        _this.ww.map((item,index)=>{
          newarr.push(Object.assign(item,{url:""}))
        })
        for(var i = 0; i< _this.ww.length;i++){
          _this.findImg(newarr,i);
        }
      })

    },
    findLike(){//按条件模糊查询文物信息
      let _this = this;
      this.$axios.get('http://8.130.9.237:8083/ww/findLike?wwname='+this.wwname+'&wwlocation1='+this.pname+'&wwlocation2='+this.cname+'&wwlocation3='+this.bname+'&wwyear='+this.dynasty+'&protectionlevel='+this.level+'&wwcategory='+this.sort).then((res)=>{
        _this.ww = res.data.results;
        let newarr = [];
        _this.ww.map((item,index)=>{//添加url属性来保存图片路径
          newarr.push(Object.assign(item,{url:""}))
        })
        for(var i = 0; i< _this.ww.length;i++){
          _this.findImg(newarr,i);//获得文物的第一个图片路径用于显示
        }
      })
    },
    findImg(newarr,i){//获取newarr的第一个图片路径
      let _this = this;
      _this.$axios.get('http://8.130.9.237:8083/ww/findAllByparentId?parentId='+_this.ww[i].wwid).then((res)=>{
        let files = res.data.results;
        newarr[i].url= "http://8.130.9.237:8083/image/"+files[0].name;//拼接获得第一个图片路径
        _this.ww[i]=newarr[i];
      });
    },
    addWw(){
      this.form.serialnum= this.dateFormat();//获得流水号
      let user=sessionStorage.getItem('info');//获得登记人信息
      if(this.form.wwname == null || this.form.wwname == ""){
        this.$message("请输入文物名称");
        return;
      }else if(this.form.protectionlevel == null || this.form.protectionlevel ==""){
        this.$message("请选择保护级别");
        return;
      }else if(this.form.wwcategory == null || this.form.wwcategory ==""){
        this.$message("请选择文物种类");
        return;
      }else if(this.form.publishingbatch == null || this.form.publishingbatch ==""){
        this.$message("请输入公布批次");
        return;
      }else if(this.form.publishingyear == null || this.form.publishingyear ==""){
        this.$message("请输入公布年份");
        return;
      }else if(this.form.wwlocation1 == null || this.form.wwlocation1 ==""){
        this.$message("请输入所在省");
        return;
      }else if(this.form.wwlocation2 == null || this.form.wwlocation2 ==""){
        this.$message("请输入所在市");
        return;
      }else if(this.form.wwlocation3 == null || this.form.wwlocation3 ==""){
        this.$message("请输入所在区");
        return;
      }else if(this.form.specificlocation == null || this.form.specificlocation ==""){
        this.$message("请输入详细地址");
        return;
      }else if(this.form.wworganization == null || this.form.wworganization ==""){
        this.$message("请输入管理单位");
        return;
      }else if(this.form.wwlatitude1 == null || this.form.wwlatitude1 ==""){
        this.$message("请输入纬度");
        return;
      }else if(this.form.wwlongitude1 == null || this.form.wwlongitude1 ==""){
        this.$message("请输入经度");
        return;
      }else if(this.form.wwelevation == null || this.form.wwelevation ==""){
        this.$message("请输入海拔");
        return;
      }else if(this.form.wwyear == null || this.form.wwyear ==""){
        this.$message("请输入年份");
        return;
      }
      user = JSON.parse(user);
      this.form.registerid=user.username;//获得登记人姓名
      this.$axios.post('http://8.130.9.237:8083/ww/add',//发送请求添加文物信息
          qs.stringify(this.form)
      ).then((res)=>{
        if(res.data.success){
          this.$axios.get('http://8.130.9.237:8083/ww/findOneByNum?serialnum='+this.form.serialnum).then((res)=>{
            let ww = res.data;
            sessionStorage.setItem('wwinfo',JSON.stringify(ww));
            this.$message("添加成功，跳转详情页面添加图片")
            this.$router.push('/home/administrator/basic');
          });
        }
      });
    },
    undoing(){
      this.dialogFormVisible = false;
    },
    doing(){
      this.dialogFormVisible = false;
      this.addWw();
    },
    // 加载china地点数据，三级
    getCityData() {
      let that = this;
      that.ChineseDistricts.forEach(function(item, index) {
        //省级数据
        that.province1.push({
          id: item.code,
          value: item.name,
          children: item.cityList
        });
        that.province2.push({
          id: item.code,
          value: item.name,
          children: item.cityList
        });
      });
    },
    // 选省
    choseProvince1(e) {
      let that = this;
      that.city1 = [];
      that.block1 = [];
      that.cname = "";
      that.bname = "";
      for (var index2 in that.province1) {
        if (e === that.province1[index2].id) {
          that.shi1 = that.province1[index2].children;
          that.pname = that.province1[index2].value;

          that.shi1.forEach(function(citem, cindex) {
            that.city1.push({
              id: citem.code,
              value: citem.name,
              children: citem.areaList
            });
          });
        }
      }
    },
    // 选市
    choseCity1(e) {
      let that = this;
      that.block1 = [];

      for (var index3 in that.city1) {
        if (e === that.city1[index3].id) {
          that.qu1 = that.city1[index3].children;

          that.cname = that.city1[index3].value;

          that.E = that.qu1[0].id;

          that.qu1.forEach(function(bitem, bindex) {
            that.block1.push({
              id: bitem.code,
              value: bitem.name,
              children: []
            });
          });
        }
      }
    },
    // 选区
    choseBlock1(e) {
      this.bname = e;
    },
    // 选省
    choseProvince2(e) {
      let that = this;
      that.city2 = [];
      that.block2 = [];
      that.form.wwlocation2 = "";
      that.form.wwlocation3 = "";
      for (var index2 in that.province2) {
        if (e === that.province2[index2].id) {
          that.shi2 = that.province2[index2].children;
          that.form.wwlocation1 = that.province2[index2].value;

          that.shi2.forEach(function(citem, cindex) {
            that.city2.push({
              id: citem.code,
              value: citem.name,
              children: citem.areaList
            });
          });
        }
      }
    },
    // 选市
    choseCity2(e) {
      let that = this;
      that.block2 = [];

      for (var index3 in that.city2) {
        if (e === that.city2[index3].id) {
          that.qu2 = that.city2[index3].children;

          that.form.wwlocation2 = that.city2[index3].value;

          that.E = that.qu2[0].id;

          that.qu2.forEach(function(bitem, bindex) {
            that.block2.push({
              id: bitem.code,
              value: bitem.name,
              children: []
            });
          });
        }
      }
    },
    // 选区
    choseBlock2(e) {
      this.form.wwlocation3 = e;
    }
  },
  created(){
    let user=sessionStorage.getItem('info');
    this.user = JSON.parse(user);
    this.getCityData();
    this.findAll();
  }
}
