

export default {
  name: "firstPage",
  data(){
    return{
      testinformation:[],
      fourinformation:[],
      num:0,
    }
  },
  computed:{
    option1(){
      return {
        backgroundColor: '#2c343c',
        title: {
          text: '文物保存总体状态',
          left: 'center',
          top: 20,
          textStyle: {
            color: '#ccc'
          }
        },

        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)',
          backgroundColor: 'rgba(255,255,255,0.6)',
          textStyle: {
            color: 'black'
          }
        },

        visualMap: {
          show: false,
          min: 0,
          max: 5,
          inRange: {
            colorLightness: [0, 1]
          }
        },
        series: [
          {
            top:40,
            name: '保存状态',
            type: 'pie',
            radius: '55%',
            center: ['50%', '50%'],
            data: this.testinformation.sort(function (a, b) {
              return a.value - b.value;
            }),
            roseType: 'radius',
            label: {
              color: 'rgba(255, 255, 255, 0.3)'
            },
            labelLine: {
              lineStyle: {
                color: 'rgba(255, 255, 255, 0.3)'
              },
              smooth: 0.2,
              length: 10,
              length2: 20
            },
            itemStyle: {
              color: '#c23531',
              shadowBlur: 200,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            },
            animationType: 'scale',
            animationEasing: 'elasticOut',
            animationDelay: function (idx) {
              return Math.random() * 200;
            }
          }
        ]
      }
    },
    option2(){
      return {
        backgroundColor: '#2c343c',
        title: {
          text: '文物四有档案总体完整状态',
          left: 'center',
          top: 20,
          textStyle: {
            color: '#ccc'
          }
        },

        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)',
          backgroundColor: 'rgba(0,0,0,0.6)',
          textStyle: {
            color: 'white'
          }
        },

        visualMap: {
          show: false,
          min: 0,
          max: 5,
          inRange: {
            colorLightness: [0, 1]
          }
        },
        series: [
          {
            top:40,
            name: '完整状态',
            type: 'pie',
            radius: '55%',
            center: ['50%', '50%'],
            data: this.fourinformation.sort(function (a, b) {
              return a.value - b.value;
            }),
            roseType: 'radius',
            label: {
              color: 'rgba(255, 255, 255, 0.3)'
            },
            labelLine: {
              lineStyle: {
                color: 'rgba(255, 255, 255, 0.3)'
              },
              smooth: 0.2,
              length: 10,
              length2: 20
            },
            itemStyle: {
              color: '#668B78',
              shadowBlur: 200,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            },

            animationType: 'scale',
            animationEasing: 'elasticOut',
            animationDelay: function (idx) {
              return Math.random() * 200;
            }
          }
        ]
      }
    },

  },
  methods:{
    findAllNum(){
      this.$axios.get('http://8.130.9.237:8083/ww/findAll').then((res)=>{
        this.num = res.data.num;
      });
    },
    getFour(){
      this.$axios.get('http://8.130.9.237:8083/four/getFour').then((res)=>{
        let one = res.data.one;
        let two = res.data.two;
        let three = res.data.three;
        let four = res.data.four;
        let zero = res.data.zero;
        this.fourinformation.push(
            {value:one, name: '1/4四有档案'},
        );
        this.fourinformation.push(
            {value:two, name: '2/4四有档案'},
        );
        this.fourinformation.push(
            {value:three, name: '3/4四有档案'},
        );
        this.fourinformation.push(
            {value:four, name: '4/4四有档案'},
        );
        this.fourinformation.push(
            {value:zero, name: '0/4四有档案'},
        );
      });
    },
    getStatus(){
      this.$axios.get('http://8.130.9.237:8083/test/getStatus').then((res)=>{
        let best = res.data.best;
        let good = res.data.good;
        let normal = res.data.normal;
        let bad = res.data.bad;
        let zero = res.data.zero;
        this.testinformation.push(
            {value:best, name: '优'},
        );
        this.testinformation.push(
            {value:good, name: '良好'},
        );
        this.testinformation.push(
            {value:normal, name: '一般'},
        );
        this.testinformation.push(
            {value:bad, name: '差'},
        );
        this.testinformation.push(
            {value:zero, name: '无健康检查'},
        );
      });
    }
  },
  created() {
    this.findAllNum();
    this.getStatus();
    this.getFour()
  }
}
